body { margin: 0; padding: 0; font-family: "roboto-regular", Arial, sans-serif; font-size: 14px; background-color: #ffffff; }

a { text-decoration: none; color: #252525; }

a:hover, a:active, a:focus { outline: 0; text-decoration: none; }

a img { border: none; }

.hide { display: none; }

.clearfix:before, .clearfix:after { content: " "; display: table; }

.clearfix:after { clear: both; }

h3 { font-family: "roboto-regular", Arial, sans-serif; font-size: 20px; margin: 0; }

.padTop15 { padding-top: 15px; }

.padTop10 { padding-top: 10px; }

.main-wrapper { width: 100%; }

.main-content { max-width: 1140px; margin-left: auto; margin-right: auto; padding-left: 0.9375rem; padding-right: 0.9375rem; }

.site-header { background: rgba(234, 159, 195, 0.8); }

.site-header a { color: #555; text-decoration: none; }

.site-footer { background: rgba(234, 159, 195, 0.8); text-align: center; font-size: 12px; padding: 1em; }

.logo { float: left; line-height: 2rem; font-size: 1.5rem; padding: 0.5em; }

.content { width: 100%; min-height: 550px; padding-bottom: 30px; }

.gm-wrapper { width: 100%; }

.gm-top { padding-top: 10px; }

.gm-top-left { float: left; padding-left: 15px; }

.gm-top-left h3 { font-weight: normal; font-size: 14px; }

.gm-top-left h3 span { font-weight: bold; }

.gm-top-right { float: right; padding-right: 15px; }

.gm-bottom { width: 520px; border-radius: 0.25rem; margin: 2em auto; padding: 15px; font-size: 16px; }

.gm-bottom.success { background-color: #dff0d8; border-color: #d0e9c6; color: #3c763d; }

.gm-table { padding-top: 30px; margin: 0 auto; width: 400px; }

.gm-table > section { float: left; background-color: #660a4d; border-radius: 3px; float: left; font-size: 15px; margin-left: 5px; margin-top: 5px; text-align: center; width: 70px; height: 70px; cursor: pointer; }

.gm-table > section span { color: #FFFFFF; display: block; margin-top: 25px; }

.gm-table > section.matched { background-color: #5a660a; cursor: initial; }
